import { Component, OnInit, ViewChild } from '@angular/core';
import { IonInfiniteScroll, IonContent, ModalController, AlertController } from '@ionic/angular';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { InventariosService } from 'src/app/providers/Api/Inventarios.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { ActivatedRoute } from '@angular/router';
import { IvtModalPage } from '../ivt-modal/ivt-modal.page';
import { Socket } from 'ngx-socket-io';

@Component({
  selector: 'app-ivtsalidas',
  templateUrl: './ivtsalidas.page.html',
  styleUrls: ['./ivtsalidas.page.scss'],
})
export class IVTsalidasPage implements OnInit {

  salidas = [];
  load = false;

  fecha1 = '';
  fecha2 = '';

  formato1 = 'AAAA-MM-DDTHH: mm: ssTZD';
  formato2 = 'AAAA-MM-DDTHH: mm: ssTZD';

  filtros = false;

  partida = 0;
  partida2 = 0;

  patron = '';

  carga = true;

  @ViewChild('inifinito', {static: false}) inifinito: IonInfiniteScroll;
  @ViewChild('contenido', {static: false}) contenido: IonContent;
  
  constructor(
    private modalCtrl: ModalController,
    private almacenamiento: InicioService,
    private ivtApi: InventariosService,
    private toast: ToastServices,
    private alertCtrl: AlertController,
    private ActiveRouter: ActivatedRoute,
    private sockect: Socket
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.cargarsalidas();
  }

  refrescando( event ) {
    this.partida = 0;
      this.partida2 = 0;
    this.cargarsalidas().then(() => {
      
      event.target.complete();
    }).catch(() => event.target.complete());
  }


  async cargarsalidas() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.carga = true;
      this.ivtApi.leerSalida(info.token, {ivt: this.ActiveRouter.snapshot.paramMap.get('id'), partida: this.partida2, patron: this.patron}).subscribe((data: any) => {
        console.log(data, 'mi data');
        
        if ( data.exe ){
           this.salidas = data.response;
        } else {
          this.toast.toastError('No se pudo listar las salidas, intentelo más tarde');
        }
      }, (err) => {
        this.toast.toastError('No se pudo listar las salidas, intentelo más tarde');
      })
     
    }
}

  async modal() {
    const modal = await this.modalCtrl.create({
      component: IvtModalPage,
      componentProps: {
        tipo: 'salidas',
        creador: this.ActiveRouter.snapshot.paramMap.get('creador'),
        inventario: this.ActiveRouter.snapshot.paramMap.get('id')
      },
    });

    await modal.present();

    const cerrado = await modal.onWillDismiss();

    if (cerrado.data) {
      if (cerrado.data.exe) {
        this.cargarsalidas();
      }
    }
  }

  async eliminar ( entrada, i) {

  }

  async editar( entrada ) {

  }

  filtro ( event, tipo ) {

    if (tipo === 1) {
        this.fecha1 = event.detail.value;
    } else {
      this.fecha2 = event.detail.value;
    }
  }

  async filtrar() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.partida = 0;

      if (this.fecha1 !== '' && this.fecha2 !== '') {
        this.ivtApi.filtrarSalidas(info.token, {desde: this.fecha1, hasta: this.fecha2, partida: this.partida}).subscribe((data: any) => {
          console.log(data, 'mi data');
          if ( data.exe ){
             this.salidas = data.response;
             this.filtros = false;
          } else {
            this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
        })
      } else {
        this.toast.toastError('Debe de especificar las fechas (desde, hasta)');
      }
     
    }
  }

  async loadData( event ) {
    // event.target.complete()

    console.log(this.partida, this.partida2)

    this.partida = this.partida + 10;

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

      if (this.fecha1 !== '' && this.fecha2 !== '') {

      this.ivtApi.filtrarSalidas(info.token, {desde: this.fecha1, hasta: this.fecha2, partida: this.partida, patron: this.patron}).subscribe((data: any) => {
        console.log(data, 'mi data scroll');
        if ( data.exe ){
           if ( data.response.length > 0  ) {
              data.response.forEach((item) => {
                this.salidas.push(item)
              })
              event.target.complete();
           } else {
            event.target.disabled = true;
            this.partida = 0;
           }
           
        } else {
          this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
        }
      }, (err) => {
        this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
      })

    } else {

      this.partida2 =  this.partida2  + 10;
      this.ivtApi.leerSalida(info.token, {ivt: this.ActiveRouter.snapshot.paramMap.get('id'), partida: this.partida2, patron: this.patron}).subscribe((data: any) => {
        console.log(data, 'mi data');
        if ( data.exe ){
          if ( data.response.length > 0  ) {
            data.response.forEach((item) => {
              this.salidas.push(item)
            })
            event.target.complete();
         } else {
          event.target.disabled = true;
          this.partida2 = 0;
         }
        } else {
          this.toast.toastError('No se pudo listar las salidas, intentelo más tarde');
        }
      }, (err) => {
        this.toast.toastError('No se pudo listar las salidas, intentelo más tarde');
      })
    }
    }
    
  }

  async buscarProducto( event ){
    this.partida = 0;
    this.partida2 = 0;
    const info = await this.almacenamiento.obtenerToken();
    await this.contenido.scrollToTop();

    if ( info ) {
      if (event.detail.value === '') {
        if (this.fecha1 !== '' && this.fecha2 !== '' && this.filtros) {
          this.filtrar();
        } else {
          this.cargarsalidas();
        }
      } else {

        this.patron = event.detail.value;

        if (this.fecha1 !== '' && this.fecha2 !== '' && this.filtros) {
            this.ivtApi.filtrarEntradasPatron(info.token, {desde: this.fecha1, hasta: this.fecha2, patron: event.detail.value, tipo: 'filtro'}).subscribe((data: any) => {
              console.log(data, 'mi data filtro');
              if ( data.exe ){
                if (data.response.length > 0) {
                  this.inifinito.disabled = false;
                } else {
                  this.inifinito.disabled = true;
                }
                this.salidas = data.response;
              } else {
                this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
              }
            }, (err) => {
              this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
            })
        } else {
          this.ivtApi.filtrarSalidasPatron(info.token, {patron: event.detail.value, tipo: 'normal'}).subscribe((data: any) => {
            console.log(data, 'mi data filtro 2');
            if ( data.exe ){
              if (data.response.length > 0) {
                this.inifinito.disabled = false;
              } else {
                this.inifinito.disabled = true;
              }
              this.salidas = data.response;
            } else {
              this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
            }
          }, (err) => {
            this.toast.toastError('No se pudo consultar las salidas, intentelo más tarde');
          })
        }

      }
    }
  }

  async borrar() {
    this.fecha1 = '';
    this.fecha2 = '';
    await this.contenido.scrollToTop();
    this.partida = 0;
    this.partida2 = 0;
    this.inifinito.disabled = false;
    this.cargarsalidas();
  }

  toggleFiltro() {
    this.filtros = !this.filtros;
  }

}
